.header {
	display: flex;
	flex-direction: column;
	justify-content: center;
	padding-top: 20px;
	padding-bottom: 50px;
	min-height: 100vh;
	background: url('../img/bg-header.jpg') center top no-repeat;
	background-size: cover;
	@include r(414) {
			padding-bottom: 15px; }
	@include r(375) {
			padding-bottom: 10px; }
	@include r(360) {
			padding-bottom: 5px; }
	&-inner {
		display: grid;
		grid-template-columns: 1fr 1fr;
		grid-gap: 40px;
		align-items: center;
		max-width: 1024px;
		margin-right: auto;
		margin-left: auto;
		@include r(1023) {
			display: block; } } }

.pics {
	margin-bottom: 20px;
	width: 285px;
	@include clr;
	&__product {
		float: left; }
	&__signs {
		float: right; }
	@include r(360) {
		margin-top: 5px;
		margin-bottom: 5px; } }

.varyforte, .guarantee, .natural {
	margin: 0 auto; }

.varyforte {
	background: url('../img/img.png') no-repeat 0 -2px;
	width: 124px;
	height: 318px; }

.guarantee {
	background: url('../img/img.png') no-repeat -181px 0;
	width: 161px;
	height: 149px; }

.natural {
	background: url('../img/img.png') no-repeat -187px -174px;
	width: 155px;
	height: 148px; }

.banner {
	padding: 20px 10px;
	background-color: rgba(51, 67, 41, 0.8);
	font-family: 'Acme', sans-serif;
	font-weight: normal;
	text-align: center;
	word-wrap: break-word;
	word-break: break-word;
	&__txt {
		margin-left: auto;
		margin-right: auto;
		max-width: 600px;
		font-size: 28px;
		color: #fcf49e;
		&--big {
			font-size: 48px;
			color: #fff; }
		&--big-desc {
			font-family: "Catamaran", sans-serif;
			font-size: 28px;
			font-weight: bold;
			color: #fbf39d; }
		@include r(414) {
			font-size: 24px;
			&--big {
				font-size: 28px; } }
		@include r(375) {
			font-size: 20px;
			&--big {
				font-size: 22px; } } }
	@include r(360) {
		padding: 10px; } }


.ask-btn {
	display: block;
	margin: 50px auto 0px auto;
	width: 300px;
	height: 80px;
	border: 6px solid #34442a;
	border-radius: 12px;
	background-clip: padding-box;
	background-color: #fcf49e;
	font-family: "Acme", sans-serif;
	font-size: 48px;
	font-weight: normal;
	color: #34442a;
	line-height: 68px;
	text-align: center;
	text-decoration: none;
	@include r(414) {
		margin: 15px auto 0 auto;
		font-size: 36px;
		height: 72px;
		line-height: 60px; }
	@include r(375) {
		margin: 10px auto 0 auto; }
	@include r(360) {
		margin: 10px auto 0 auto; }
	&--mobile {
		display: none;
		@include r(1023) {
			display: block; } } }

.description {
	background: url('data:image/png;base64,');
	background-repeat: repeat;
	.slider__arrow {
		display: none;
		@include r(1366) {
			display: block; } } }

.slider {
	position: relative;
	margin-bottom: 10px;
	margin-right: auto;
	margin-left: auto;
	@include r(768) {
		max-width: 720px; }
	&__arrow {
		position: absolute;
		padding: 10px;
		top: 50%;
		transform: translateY(-50%);
		font-size: 0px;
		&--prev {
			left: 0; }
		&--next {
			right: 0; }
		svg {
			height: 70px;
			fill: $v_orange;
			cursor: pointer; } } }
.sl {
	&__pic {
		margin: 15px auto 24px auto;
		padding-bottom: 24px;
		border-bottom: 3px solid $v_green;
		box-sizing: content-box;
		&--water {
			background: url('../img/img.png') no-repeat -162px -532px;
			width: 115px;
			height: 139px; }
		&--foot {
			background: url('../img/img.png') no-repeat -3px -518px;
			width: 99px;
			height: 167px; }
		&--blood {
			background: url('../img/img.png') no-repeat -170px -332px;
			width: 172px;
			height: 161px; }
		&--shield {
			background: url('../img/img.png') no-repeat -3px -344px;
			width: 142px;
			height: 149px; } }
	&--card {
		padding: 128px 45px 0px 45px; }
	&__card {
		position: relative;
		padding-top: 120px;
		margin: 0 auto;
		max-width: 380px;
		border: 6px solid #455939;
		border-radius: 11px; }
	&__icon {
		position: absolute;
		left: 50%;
		transform: translateX(-50%) translateY(calc(-50% - 126px));	// -50% - __card(padding-top) - __card(border)
		&--honey {
			background: url('../img/img.png') no-repeat -8px -718px;
			width: 214px;
			height: 214px; }
		&--flowers {
			background: url('../img/img.png') no-repeat -8px -1200px;
			width: 214px;
			height: 214px; }
		&--lemon {
			background: url('../img/img.png') no-repeat -8px -1441px;
			width: 214px;
			height: 214px; }
		&--chestnut {
			background: url('../img/img.png') no-repeat -8px -959px;
			width: 214px;
			height: 214px; }
		&--pills {
			background: url('../img/img.png') no-repeat -11px -1683px;
			width: 214px;
			height: 214px; } }
	&__photo {
		margin: 52px auto 0 auto;
		background: url('../img/img.png') no-repeat;
		width: 281px;
		height: 192px;
		&--1 {
			background-position: -379px -19px; }
		&--2 {
			background-position: -379px -241px; }
		&--3 {
			background-position: -379px -459px; } }
	&__txt {
		padding: 10px 40px;
		color: #334329;
		font-family: "Catamaran", sans-serif;
		font-size: 30px;
		font-weight: bold;
		text-align: center;
		&--user {
			color: #eb8106;
			font-family: "Acme", sans-serif;
			font-size: 28px; }
		&--comment {
			margin-right: auto;
			margin-left: auto;
			padding-right: 50px;
			padding-left: 50px;
			max-width: 380px;
			color: #333;
			font-family: "Catamaran", sans-serif;
			font-size: 18px;
			font-weight: bold; }
		h3 {
			margin-right: -35px;
			margin-left: -35px;
			color: #eb8105;
			font-family: "Acme", sans-serif;
			font-size: 36px;
			word-wrap: break-word;
			word-break: break-word; }
		ul {
			margin-right: -35px;
			margin-left: -35px; }
		li {
			display: table;
			color: #465939;
			font-family: "Catamaran", sans-serif;
			font-size: 24px;
			font-weight: bold;
			text-align: left;
			word-wrap: break-word;
			word-break: break-word;
			p {
				display: table-cell; }
			&:before {
				display: table-cell;
				position: relative;
				margin-right: 5px;
				top: 12px;
				content: "â€¢";
				font-size: 60px;
				padding-right: 5px;
				line-height: 10px;
				color: #eb8105; } } }
	&__sep {
		margin: 25px auto;
		height: 0;
		width: 40px;
		border: none;
		border-bottom: 3px solid #eb8206;
		&--long {
			width: 100px; } } }


.consist {
	padding-top: 50px;
	padding-bottom: 50px;
	@include r(414) {
		padding-top: 10px; } }

.results {
	.slider__arrow {
		display: none;
		@include r(1024) {
			display: block; } } }

.consist, .how-to-buy {
	background: url('data:image/png;base64,');
	background-repeat: repeat; }

.expert {
	padding-bottom: 50px;
	background: #fff;
	&__content {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: center;
		align-items: center;
		padding-top: 50px;
		@include r(900) {
			flex-direction: column; } } }

.color-box {
	position: relative;
	margin: 20px;
	max-width: 430px;
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	overflow: hidden;
	&__bg {
		position: absolute;
		left: 50%;
		z-index: 1;
		transform: translateX(-50%);
		background: none;
		&--legs {
			top: 0;
			background: url('../img/img.png') no-repeat -31px -1907px;
			width: 215px;
			height: 475px; }
		&--doc {
			bottom: 0;
			background: url('../img/img.png') no-repeat -264px -702px;
			width: 100%;
			max-width: 412px;
			height: 561px; } }
	&__left, &__right {
		display: flex;
		position: relative;
		z-index: 2;
		background: inherit; }
	&__left {
		p {
			text-align: left;
			background-color: rgba(111, 60, 0, .8); } }
	&__right {
		p {
			text-align: right;
			background-color: rgba(32, 77, 0, .8); } }
	&__txt {
		width: 80%;
		padding: 20px;
		color: #fff;
		font-family: "Catamaran", sans-serif;
		font-size: 24px;
		font-weight: bold;
		line-height: 1.2em;
		word-wrap: break-word;
		word-break: break-word;
		@include r(414) {
			font-size: 21px; } }
	&__space {
		flex-grow: 1;
		background: inherit;
		&--white {
			background: white; } } }

.illustrations {
	display: grid;
	grid-template-rows: 1fr;
	grid-template-columns: 1fr 1fr 1fr;
	grid-gap: 40px;
	@include r(1023) {
		display: block; } }

.ill {
	&__header {
		display: flex;
		flex-direction: row;
		align-items: center;
		background-color: #455939;
		word-wrap: break-word;
		opacity: .8;
		&:nth-child(even) {
			grid-row: 1; }
		div {
			padding: 10px;
			flex-basis: 50%;
			font-size: 0;
			svg {
				float: right;
				max-width: 165px; } }
		p {
			padding: 0 10px;
			flex-basis: 50%;
			max-width: 200px;
			align-self: center;
			color: #fbf39d;
			font-family: "Acme", sans-serif;
			font-size: 36px; } }
	&__txt {
		padding: 20px;
		color: #6b7961;
		font-family: "Catamaran", sans-serif;
		font-size: 20px;
		font-weight: bold;
		text-align: center;
		word-wrap: break-word;
		word-break: break-word;
		&:nth-child(even) {
			grid-row: 2; }
		p {
			margin: 0 auto;
			max-width: 374px;
			padding: 20px;
			border: 3px solid rgba(69, 89, 57, 0.8);
			border-radius: 10px; } } }

.how-to-buy {
	padding-bottom: 60px; }

.step {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
	@include r(1023) {
		flex-direction: column; }
	&__1 {
		margin: 30px auto;
		padding: 12px 15px;
		max-width: 288px;
		border-radius: 10px;
		background-clip: padding-box;
		background-color: rgba(37, 181, 209, 0.8); }
	&__2 {
		margin: 30px auto;
		padding: 12px 15px;
		max-width: 288px;
		border-radius: 10px;
		background-clip: padding-box;
		background-color: rgba(101, 45, 87, 0.8); }
	&__3 {
		margin: 30px auto;
		padding: 12px 15px;
		max-width: 288px;
		border-radius: 10px;
		background-clip: padding-box;
		background-color: rgba(241, 107, 79, 0.8); }
	&__pic {
		margin: 0 auto;
		width: 150px;
		font-size: 0; }
	&__txt {
		color: #fff;
		font-family: "Catamaran", sans-serif;
		font-size: 20px;
		font-weight: bold;
		text-align: center;
		line-height: 1.2em; }
	&__arr-down {
		margin: 0 auto;
		width: 73px;
		transform: rotate(-90deg);
		@include r(1023) {
			transform: rotate(0); } } }

.order {
	display: flex;
	justify-content: center;
	align-items: center;
	min-height: 768px;
	padding-top: 60px;
	padding-right: 10px;
	padding-bottom: 60px;
	padding-left: 10px;
	background: url('../img/bg-footer.jpg');
	background-size: cover;
	background-position-y: center;
	background-position-x: 25%;
	background-repeat: no-repeat;
	&-inner {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: space-around;
		align-items: center;
		flex-grow: 1;
		max-width: 1024px;
		@include r(1024) {
			//display: block

			min-height: auto; } } }

.price {
	margin: 20px auto;
	max-width: 280px;
	text-align: center;
	font-family: "Acme", sans-serif;
	&__old {
		margin-right: 10px;
		color: #a9a9a9;
		font-size: 36px; }
	&__new {
		margin-left: 10px;
		color: #ee6303;
		font-size: 78px; }
	&--green {
		.price__new {
			color: $v_green; }
		.price__old {
			color: $v_green; } } }

.order-form {
	display: flex;
	flex-direction: column;
	margin: 0 auto;
	max-width: 300px;
	input {
		margin-bottom: 12px;
		height: 80px;
		background: none;
		border: none;
		border: 6px solid #fcf49e;
		border-radius: 14px;
		color: #fbf39d;
		font-family: "Acme", sans-serif;
		font-size: 36px;
		&::-webkit-input-placeholder {
			color: #fbf39d; }
		&::-moz-placeholder /* Firefox 19+ */ {
			color: #fbf39d; }
		&:-moz-placeholder /* Firefox 18- */ {
			color: #fbf39d; }
		&:-ms-input-placeholder {
			color: #fbf39d; } }
	input[type="text"] {
		padding: 0 15px; }
	input[type="submit"] {
		margin-top: 23px;
		background-color: #fcf49e;
		color: #34442a;
		font-size: 48px; }
	&--green {
		input {
			border-color: $v_green;
			&::-webkit-input-placeholder {
				color: $v_green; }
			&::-moz-placeholder /* Firefox 19+ */ {
				color: $v_green; }
			&:-moz-placeholder /* Firefox 18- */ {
				color: $v_green; }
			&:-ms-input-placeholder {
				color: $v_green; } } }
	&--desktop {
		@include r(1023) {
			display: none; } } }

.footer {
	padding-bottom: 50px;
	padding-top: 18px;
	background-color: #202a1a;
	text-align: center; }

.doc-links {
	a {
		margin: 18px;
		color: #fff;
		font-family: "Catamaran", sans-serif;
		font-size: 20px;
		text-decoration: none;
		@include r(767) {
			display: block;
			margin-top: 36px;
			margin-bottom: 36px; } } }

.copyright {
	margin-top: 40px;
	color: #878c84;
	font-family: "Catamaran", sans-serif;
	font-size: 20px; }
