@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

html { -ms-text-size-adjust: 100%; -webkit-text-size-adjust: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

input[type="submit"], button { cursor: pointer; }

input[type="submit"]::-moz-focus-inner, button::-moz-focus-inner { padding: 0; border: 0; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

/* Slider */
.slick-slider { position: relative; display: block; box-sizing: border-box; -moz-box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }

.slick-list:focus { outline: none; }

.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-track, .slick-slider .slick-list { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; }

.slick-track:before, .slick-track:after { content: ""; display: table; }

.slick-track:after { clear: both; }

.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; display: none; }

[dir="rtl"] .slick-slide { float: right; }

.slick-slide img { display: block; }

.slick-slide.slick-loading img { display: none; }

.slick-slide.dragging img { pointer-events: none; }

.slick-initialized .slick-slide { display: block; }

.slick-loading .slick-slide { visibility: hidden; }

.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

body { background-color: #fff; }

.header { display: flex; flex-direction: column; justify-content: center; padding-top: 20px; padding-bottom: 50px; min-height: 100vh; background: url("../img/bg-header.jpg") center top no-repeat; background-size: cover; }

@media only screen and (max-width: 414px) { .header { padding-bottom: 15px; } }

@media only screen and (max-width: 375px) { .header { padding-bottom: 10px; } }

@media only screen and (max-width: 360px) { .header { padding-bottom: 5px; } }

.header-inner { display: grid; grid-template-columns: 1fr 1fr; grid-gap: 40px; align-items: center; max-width: 1024px; margin-right: auto; margin-left: auto; }

@media only screen and (max-width: 1023px) { .header-inner { display: block; } }

.pics { margin-bottom: 20px; width: 285px; *zoom: 1; }

.pics:after { content: " "; display: table; clear: both; }

.pics__product { float: left; }

.pics__signs { float: right; }

@media only screen and (max-width: 360px) { .pics { margin-top: 5px; margin-bottom: 5px; } }

.varyforte, .guarantee, .natural { margin: 0 auto; }

.varyforte { background: url("../img/img.png") no-repeat 0 -2px; width: 124px; height: 318px; }

.guarantee { background: url("../img/img.png") no-repeat -181px 0; width: 161px; height: 149px; }

.natural { background: url("../img/img.png") no-repeat -187px -174px; width: 155px; height: 148px; }

.banner { padding: 20px 10px; background-color: rgba(51, 67, 41, 0.8); font-family: 'Acme', sans-serif; font-weight: normal; text-align: center; word-wrap: break-word; word-break: break-word; }

.banner__txt { margin-left: auto; margin-right: auto; max-width: 600px; font-size: 28px; color: #fcf49e; }

.banner__txt--big { font-size: 48px; color: #fff; }

.banner__txt--big-desc { font-family: "Catamaran", sans-serif; font-size: 28px; font-weight: bold; color: #fbf39d; }

@media only screen and (max-width: 414px) { .banner__txt { font-size: 24px; }
  .banner__txt--big { font-size: 28px; } }

@media only screen and (max-width: 375px) { .banner__txt { font-size: 20px; }
  .banner__txt--big { font-size: 22px; } }

@media only screen and (max-width: 360px) { .banner { padding: 10px; } }

.ask-btn { display: block; margin: 50px auto 0px auto; width: 300px; height: 80px; border: 6px solid #34442a; border-radius: 12px; background-clip: padding-box; background-color: #fcf49e; font-family: "Acme", sans-serif; font-size: 48px; font-weight: normal; color: #34442a; line-height: 68px; text-align: center; text-decoration: none; }

@media only screen and (max-width: 414px) { .ask-btn { margin: 15px auto 0 auto; font-size: 36px; height: 72px; line-height: 60px; } }

@media only screen and (max-width: 375px) { .ask-btn { margin: 10px auto 0 auto; } }

@media only screen and (max-width: 360px) { .ask-btn { margin: 10px auto 0 auto; } }

.ask-btn--mobile { display: none; }

@media only screen and (max-width: 1023px) { .ask-btn--mobile { display: block; } }

.description { background: url("data:image/png;base64,"); background-repeat: repeat; }

.description .slider__arrow { display: none; }

@media only screen and (max-width: 1366px) { .description .slider__arrow { display: block; } }

.slider { position: relative; margin-bottom: 10px; margin-right: auto; margin-left: auto; }

@media only screen and (max-width: 768px) { .slider { max-width: 720px; } }

.slider__arrow { position: absolute; padding: 10px; top: 50%; transform: translateY(-50%); font-size: 0px; }

.slider__arrow--prev { left: 0; }

.slider__arrow--next { right: 0; }

.slider__arrow svg { height: 70px; fill: #eb8206; cursor: pointer; }

.sl__pic { margin: 15px auto 24px auto; padding-bottom: 24px; border-bottom: 3px solid #46593a; box-sizing: content-box; }

.sl__pic--water { background: url("../img/img.png") no-repeat -162px -532px; width: 115px; height: 139px; }

.sl__pic--foot { background: url("../img/img.png") no-repeat -3px -518px; width: 99px; height: 167px; }

.sl__pic--blood { background: url("../img/img.png") no-repeat -170px -332px; width: 172px; height: 161px; }

.sl__pic--shield { background: url("../img/img.png") no-repeat -3px -344px; width: 142px; height: 149px; }

.sl--card { padding: 128px 45px 0px 45px; }

.sl__card { position: relative; padding-top: 120px; margin: 0 auto; max-width: 380px; border: 6px solid #455939; border-radius: 11px; }

.sl__icon { position: absolute; left: 50%; transform: translateX(-50%) translateY(calc(-50% - 126px)); }

.sl__icon--honey { background: url("../img/img.png") no-repeat -8px -718px; width: 214px; height: 214px; }

.sl__icon--flowers { background: url("../img/img.png") no-repeat -8px -1200px; width: 214px; height: 214px; }

.sl__icon--lemon { background: url("../img/img.png") no-repeat -8px -1441px; width: 214px; height: 214px; }

.sl__icon--chestnut { background: url("../img/img.png") no-repeat -8px -959px; width: 214px; height: 214px; }

.sl__icon--pills { background: url("../img/img.png") no-repeat -11px -1683px; width: 214px; height: 214px; }

.sl__photo { margin: 52px auto 0 auto; background: url("../img/img.png") no-repeat; width: 281px; height: 192px; }

.sl__photo--1 { background-position: -379px -19px; }

.sl__photo--2 { background-position: -379px -241px; }

.sl__photo--3 { background-position: -379px -459px; }

.sl__txt { padding: 10px 40px; color: #334329; font-family: "Catamaran", sans-serif; font-size: 30px; font-weight: bold; text-align: center; }

.sl__txt--user { color: #eb8106; font-family: "Acme", sans-serif; font-size: 28px; }

.sl__txt--comment { margin-right: auto; margin-left: auto; padding-right: 50px; padding-left: 50px; max-width: 380px; color: #333; font-family: "Catamaran", sans-serif; font-size: 18px; font-weight: bold; }

.sl__txt h3 { margin-right: -35px; margin-left: -35px; color: #eb8105; font-family: "Acme", sans-serif; font-size: 36px; word-wrap: break-word; word-break: break-word; }

.sl__txt ul { margin-right: -35px; margin-left: -35px; }

.sl__txt li { display: table; color: #465939; font-family: "Catamaran", sans-serif; font-size: 24px; font-weight: bold; text-align: left; word-wrap: break-word; word-break: break-word; }

.sl__txt li p { display: table-cell; }

.sl__txt li:before { display: table-cell; position: relative; margin-right: 5px; top: 12px; content: "â€¢"; font-size: 60px; padding-right: 5px; line-height: 10px; color: #eb8105; }

.sl__sep { margin: 25px auto; height: 0; width: 40px; border: none; border-bottom: 3px solid #eb8206; }

.sl__sep--long { width: 100px; }

.consist { padding-top: 50px; padding-bottom: 50px; }

@media only screen and (max-width: 414px) { .consist { padding-top: 10px; } }

.results .slider__arrow { display: none; }

@media only screen and (max-width: 1024px) { .results .slider__arrow { display: block; } }

.consist, .how-to-buy { background: url("data:image/png;base64,"); background-repeat: repeat; }

.expert { padding-bottom: 50px; background: #fff; }

.expert__content { display: flex; flex-direction: row; flex-wrap: wrap; justify-content: center; align-items: center; padding-top: 50px; }

@media only screen and (max-width: 900px) { .expert__content { flex-direction: column; } }

.color-box { position: relative; margin: 20px; max-width: 430px; display: flex; flex-wrap: wrap; flex-direction: column; overflow: hidden; }

.color-box__bg { position: absolute; left: 50%; z-index: 1; transform: translateX(-50%); background: none; }

.color-box__bg--legs { top: 0; background: url("../img/img.png") no-repeat -31px -1907px; width: 215px; height: 475px; }

.color-box__bg--doc { bottom: 0; background: url("../img/img.png") no-repeat -264px -702px; width: 100%; max-width: 412px; height: 561px; }

.color-box__left, .color-box__right { display: flex; position: relative; z-index: 2; background: inherit; }

.color-box__left p { text-align: left; background-color: rgba(111, 60, 0, 0.8); }

.color-box__right p { text-align: right; background-color: rgba(32, 77, 0, 0.8); }

.color-box__txt { width: 80%; padding: 20px; color: #fff; font-family: "Catamaran", sans-serif; font-size: 24px; font-weight: bold; line-height: 1.2em; word-wrap: break-word; word-break: break-word; }

@media only screen and (max-width: 414px) { .color-box__txt { font-size: 21px; } }

.color-box__space { flex-grow: 1; background: inherit; }

.color-box__space--white { background: white; }

.illustrations { display: grid; grid-template-rows: 1fr; grid-template-columns: 1fr 1fr 1fr; grid-gap: 40px; }

@media only screen and (max-width: 1023px) { .illustrations { display: block; } }

.ill__header { display: flex; flex-direction: row; align-items: center; background-color: #455939; word-wrap: break-word; opacity: .8; }

.ill__header:nth-child(even) { grid-row: 1; }

.ill__header div { padding: 10px; flex-basis: 50%; font-size: 0; }

.ill__header div svg { float: right; max-width: 165px; }

.ill__header p { padding: 0 10px; flex-basis: 50%; max-width: 200px; align-self: center; color: #fbf39d; font-family: "Acme", sans-serif; font-size: 36px; }

.ill__txt { padding: 20px; color: #6b7961; font-family: "Catamaran", sans-serif; font-size: 20px; font-weight: bold; text-align: center; word-wrap: break-word; word-break: break-word; }

.ill__txt:nth-child(even) { grid-row: 2; }

.ill__txt p { margin: 0 auto; max-width: 374px; padding: 20px; border: 3px solid rgba(69, 89, 57, 0.8); border-radius: 10px; }

.how-to-buy { padding-bottom: 60px; }

.step { display: flex; flex-direction: row; justify-content: center; align-items: center; flex-wrap: wrap; }

@media only screen and (max-width: 1023px) { .step { flex-direction: column; } }

.step__1 { margin: 30px auto; padding: 12px 15px; max-width: 288px; border-radius: 10px; background-clip: padding-box; background-color: rgba(37, 181, 209, 0.8); }

.step__2 { margin: 30px auto; padding: 12px 15px; max-width: 288px; border-radius: 10px; background-clip: padding-box; background-color: rgba(101, 45, 87, 0.8); }

.step__3 { margin: 30px auto; padding: 12px 15px; max-width: 288px; border-radius: 10px; background-clip: padding-box; background-color: rgba(241, 107, 79, 0.8); }

.step__pic { margin: 0 auto; width: 150px; font-size: 0; }

.step__txt { color: #fff; font-family: "Catamaran", sans-serif; font-size: 20px; font-weight: bold; text-align: center; line-height: 1.2em; }

.step__arr-down { margin: 0 auto; width: 73px; transform: rotate(-90deg); }

@media only screen and (max-width: 1023px) { .step__arr-down { transform: rotate(0); } }

.order { display: flex; justify-content: center; align-items: center; min-height: 768px; padding-top: 60px; padding-right: 10px; padding-bottom: 60px; padding-left: 10px; background: url("../img/bg-footer.jpg"); background-size: cover; background-position-y: center; background-position-x: 25%; background-repeat: no-repeat; }

.order-inner { display: flex; flex-direction: row; flex-wrap: wrap; justify-content: space-around; align-items: center; flex-grow: 1; max-width: 1024px; }

@media only screen and (max-width: 1024px) { .order-inner { min-height: auto; } }

.price { margin: 20px auto; max-width: 280px; text-align: center; font-family: "Acme", sans-serif; }

.price__old { margin-right: 10px; color: #a9a9a9; font-size: 36px; }

.price__new { margin-left: 10px; color: #ee6303; font-size: 78px; }

.price--green .price__new { color: #46593a; }

.price--green .price__old { color: #46593a; }

.order-form { display: flex; flex-direction: column; margin: 0 auto; max-width: 300px; }

.order-form input { margin-bottom: 12px; height: 80px; background: none; border: none; border: 6px solid #fcf49e; border-radius: 14px; color: #fbf39d; font-family: "Acme", sans-serif; font-size: 36px; }

.order-form input::-webkit-input-placeholder { color: #fbf39d; }

.order-form input::-moz-placeholder { color: #fbf39d; }

.order-form input:-moz-placeholder { color: #fbf39d; }

.order-form input:-ms-input-placeholder { color: #fbf39d; }

.order-form input[type="text"] { padding: 0 15px; }

.order-form input[type="submit"] { margin-top: 23px; background-color: #fcf49e; color: #34442a; font-size: 48px; }

.order-form--green input { border-color: #46593a; }

.order-form--green input::-webkit-input-placeholder { color: #46593a; }

.order-form--green input::-moz-placeholder { color: #46593a; }

.order-form--green input:-moz-placeholder { color: #46593a; }

.order-form--green input:-ms-input-placeholder { color: #46593a; }

@media only screen and (max-width: 1023px) { .order-form--desktop { display: none; } }

.footer { padding-bottom: 50px; padding-top: 18px; background-color: #202a1a; text-align: center; }

.doc-links a { margin: 18px; color: #fff; font-family: "Catamaran", sans-serif; font-size: 20px; text-decoration: none; }

@media only screen and (max-width: 767px) { .doc-links a { display: block; margin-top: 36px; margin-bottom: 36px; } }

.copyright { margin-top: 40px; color: #878c84; font-family: "Catamaran", sans-serif; font-size: 20px; }
